html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition: none !important;
  }
}

.icon-facebook {
  width: 0.53em;
  height: 1em;
  fill: none;
}

body {
  background: #FFF;
  line-height: 1.2;
  font-size: 14px;
  min-width: 320px;
  font-family: "Lora", Arial, Helvetica, serif;
  font-weight: 400;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

a {
  text-decoration: none;
}

button {
  background: none;
}

.text-center {
  text-align: center;
}

.text-right {
  text-align: right;
}

.text-left {
  text-align: left;
}

.upper {
  text-transform: uppercase;
}

.container {
  max-width: 1570px;
  width: 100%;
  padding: 0 20px;
  margin: 0 auto;
}

@media only screen and (max-width: 1400px) {
  .container {
    max-width: 1280px;
    padding: 0 45px;
  }
}

@media only screen and (max-width: 1200px) {
  .container {
    padding: 0 30px;
  }
}

@media only screen and (max-width: 599px) {
  .container {
    padding: 0 20px;
  }
}

.l {
  display: flex;
  flex-flow: row nowrap;
}

@media only screen and (max-width: 599px) {
  .l:not(.l_not-col) {
    flex-direction: column;
  }
}

.l_stretch {
  align-items: stretch;
}

.l_center {
  align-items: center;
}

.l_between {
  justify-content: space-between;
}

.l_around {
  justify-content: space-around;
}

.l__col {
  flex: 1 0 auto;
}

@media only screen and (max-width: 599px) {
  .l__col-order-1-xs {
    order: 1;
  }
}

@media only screen and (max-width: 599px) {
  .l__col-order-2-xs {
    order: 2;
  }
}

@media only screen and (max-width: 599px) {
  .l__col-order-3-xs {
    order: 3;
  }
}

.header.is-menu-open .header__btn {
  opacity: 0;
}

.header.is-menu-open .header__menu {
  display: block;
}

.header__top {
  padding: 10px 0;
  border-bottom: 1px solid #6ecdbe;
  overflow: hidden;
}

.header__link {
  display: inline-block;
  font-size: 11px;
  letter-spacing: 1px;
  text-transform: uppercase;
  color: #303d4a;
  line-height: 1.9;
  transition: opacity 0.2s;
  font-family: "Ubuntu", Arial, Helvetica, serif;
  font-weight: 300;
}

.header__link:not(:last-child) {
  border-right: 1px solid #6ecdbe;
  padding-right: 38px;
  margin-right: 37px;
}

@media only screen and (max-width: 767px) {
  .header__link:not(:last-child) {
    padding-right: 19px;
    margin-right: 16px;
  }
}

@media only screen and (max-width: 767px) {
  .header__link:last-child {
    font-size: 10px;
  }
}

@media only screen and (max-width: 599px) {
  .header__link:last-child {
    margin-right: 0;
  }
}

.header__link:hover {
  opacity: 0.7;
}

.header__logo {
  margin-top: -8px;
}

@media only screen and (max-width: 979px) {
  .header__menu {
    display: none;
    position: absolute;
    top: 100%;
    left: 0;
    right: 0;
    background: #fff;
    z-index: 9;
    text-align: center;
  }
}

@media only screen and (min-width: 980px) {
  .header__menu-hidden {
    display: none;
  }
}

.header__content {
  position: relative;
}

@media only screen and (max-width: 599px) {
  .header__btn {
    display: none;
  }
}

.burger {
  position: relative;
  display: block;
  width: 32px;
  height: 14px;
}

@media only screen and (min-width: 980px) {
  .burger {
    display: none;
  }
}

.burger.is-active {
  width: 20px;
}

.burger.is-active span:first-child {
  transform: rotate(45deg);
  top: 6px;
}

.burger.is-active span:last-child {
  transform: rotate(-45deg);
  top: 6px;
}

.burger.is-active span:nth-child(2) {
  transform: scale(0);
}

.burger > span {
  position: absolute;
  left: 0;
  width: 100%;
  height: 2px;
  background: #f08058;
  transition: transform 0.3s ease-in-out;
}

.burger > span:first-child {
  top: 0;
}

.burger > span:nth-child(2) {
  top: 6px;
}

.burger > span:last-child {
  top: 12px;
}

.footer {
  background: #f08058;
  padding: 29px 0;
}

@media only screen and (max-width: 599px) {
  .footer {
    padding: 29px 0 12px;
  }
}

.footer__item {
  display: inline-block;
  font-size: 13px;
  line-height: 1.3;
  color: #fff;
  font-family: "Ubuntu", Arial, Helvetica, serif;
  font-weight: 300;
}

.footer__item:not(.upper) {
  max-width: 140px;
}

@media only screen and (max-width: 599px) {
  .footer__item:not(.upper) {
    max-width: 100%;
  }
}

.footer__item:not(div) {
  transition: opacity 0.15s;
}

.footer__item:not(div):hover {
  opacity: .8;
}

@media only screen and (max-width: 599px) {
  .footer__item {
    max-width: 100%;
    text-align: center;
    margin-bottom: 15px;
  }
}

.footer__item_spacing {
  letter-spacing: 1.3px;
}

.logo {
  font-size: 32px;
  line-height: 40px;
  color: #303d4a;
  letter-spacing: -1.3px;
  font-family: "Lora", Arial, Helvetica, serif;
  font-weight: 400;
  font-style: italic;
}

@media only screen and (max-width: 979px) {
  .logo {
    font-size: 24px;
    padding: 17px 0;
  }
}

.logo > span {
  color: #f08058;
}

.nav {
  display: flex;
  flex-flow: row nowrap;
  align-items: center;
  justify-content: space-between;
  margin: 0 -40px;
}

@media only screen and (max-width: 1400px) {
  .nav {
    margin: 0 -20px;
  }
}

@media only screen and (max-width: 1200px) {
  .nav {
    margin: 0 -7px;
  }
}

@media only screen and (max-width: 979px) {
  .nav {
    flex-direction: column;
    margin: 55px 0 65px;
  }
}

.nav__link {
  display: inline-block;
  font-size: 13px;
  color: #303d4a;
  margin: 0 40px;
  padding: 42px 0;
  letter-spacing: 1.1px;
  text-transform: uppercase;
  border-bottom: 1px solid transparent;
  transition: border-bottom-color 0.2s ease-in-out;
  font-family: "Ubuntu", Arial, Helvetica, serif;
  font-weight: 300;
}

.nav__link:hover {
  border-bottom-color: #6ecdbe;
}

@media only screen and (max-width: 1400px) {
  .nav__link {
    margin: 0 20px;
  }
}

@media only screen and (max-width: 1200px) {
  .nav__link {
    margin: 0 7px;
  }
}

@media only screen and (max-width: 979px) {
  .nav__link {
    padding: 15px 0;
    display: block;
    border-width: 2px;
    text-align: center;
  }
}

.btn {
  display: inline-block;
  vertical-align: middle;
  font-size: 18px;
  border-radius: 24px;
  border: 1px solid transparent;
  text-align: center;
  padding: 12px 23px 13px;
  transition: background .2s, border-color .2s;
  font-family: "Lora", Arial, Helvetica, serif;
  font-weight: 400;
  font-style: italic;
}

.btn_width {
  max-width: 100%;
}

.btn_full-orange {
  background: #f08058;
  color: #fff;
}

.btn_full-orange:hover {
  background: #e46b40;
}

.btn_full-green {
  background: #6ecdbe;
  color: #fff;
}

.btn_full-white {
  background: #fff;
  color: #f08058;
}

.btn:disabled {
  background: gray;
  color: #fff;
}

.btn_border-orange {
  border-bottom-color: #f08058;
  color: #f08058;
}

.btn_border-white {
  border-bottom-color: #fff;
  color: #fff;
}

.btn_border-green {
  border-bottom-color: #6ecdbe;
  color: #6ecdbe;
}

.btn_md {
  font-size: 22px;
  padding: 10px 20px;
}

@media only screen and (max-width: 979px) {
  .btn_sm-mob {
    font-size: 16px;
    padding: 4px 15px 7px;
  }
}
