.footer {
  background: $orange;
  padding: 29px 0;
  @include xs {
    padding: 29px 0 12px; }
  &__item {
    display: inline-block;
    font-size: 13px;
    line-height: 1.3;
    color: #fff;
    @include ubuntu-l;
    &:not(.upper) {
      max-width: 140px;
      @include xs {
        max-width: 100%; } }
    &:not(div) {
      @include tr(opacity .15s);
      &:hover {
        opacity: .8; } }
    @include xs {
      max-width: 100%;
      text-align: center;
      margin-bottom: 15px; } }
  &__item_spacing {
    letter-spacing: 1.3px; } }
