.container {
  max-width: 1570px;
  width: 100%;
  padding: 0 20px;
  margin: 0 auto;
  @include r(1400) {
    max-width: 1280px;
    padding: 0 45px; }
  @include r(1200) {
    padding: 0 30px; }
  @include xs {
    padding: 0 20px; } }



.l {
  display: flex;
  flex-flow: row nowrap;
  &:not(.l_not-col) {
    @include xs {
      flex-direction: column; } }
  &_stretch {
    align-items: stretch; }
  &_center {
    align-items: center; }
  &_between {
    justify-content: space-between; }
  &_around {
    justify-content: space-around; }

  &__col {
    flex: 1 0 auto; }
  &__col-order-1-xs {
    @include xs {
      order: 1; } }
  &__col-order-2-xs {
    @include xs {
      order: 2; } }
  &__col-order-3-xs {
    @include xs {
      order: 3; } } }
